@font-face {
    font-display: swap;
    font-family: "_Lato";
    src: url('../fonts/Lato-Regular.ttf');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-display: swap;
    font-family: "_Lato";
    src: url('../fonts/Lato-Bold.ttf');
    font-weight: bold;
    font-style: normal; }

.title-first {
    color: $color-white;
    animation-name: leftToRight;
    animation-duration: 4s;
    animation-delay: 1.5s;
    margin-bottom: 6.5rem;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        margin-bottom: 1rem; }
    &__second {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: 0.5rem; } }

body {
    font-family: "_Lato", sans-serif;
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: 0.2rem; }
